img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.contact-form {
  width: 600px;
  flex-direction: column;
  margin: 0 auto;
  padding: 40px;
  display: flex;
  box-shadow: 0 10px 10px #ccc;
}

.contact-form-input {
  height: 40px;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding-left: 15px;
}

.contact-form-message {
  height: 120px;
  resize: none;
  border: 1px solid #303030;
  margin-bottom: 20px;
  padding: 15px;
}

.contact-form-submit {
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #303030;
  border-radius: 4px;
  align-self: center;
  padding: 10px 20px;
  transition: transform .25s linear;
}

.contact-form-submit:hover, .contact-form-submit:focus {
  transform: scale(1.05);
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: #5d8781bd;
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.close-btn {
  cursor: pointer;
  width: 25px;
  height: 25px;
  background-color: #7fffd4;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 40px;
  right: 40px;
}

.nav__link {
  background: #4fb53f;
  border-radius: 8px;
  margin: 4px;
  padding: 10px 16px;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 10px 10px #ccc;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 30px;
  padding-right: 30px;
}

.gallery-section {
  padding: 60px 0;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  flex-wrap: wrap;
  margin-top: -30px;
  margin-bottom: 20px;
  margin-left: -30px;
  display: flex;
}

.gallery__item {
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-left: 30px;
}

.gallery-img {
  width: 100%;
  height: 100%;
  max-width: unset;
  object-fit: cover;
}

